<template>
	<div
		class="ds-surveyQuestionOpenEnded"
		:class="{ '-ds-disabled': state === SURVEY_QUESTION_STATES.DISABLED }"
	>
		<ds-modal v-if="showModal" @close-modal="showModal = false">
			<slot name="explanation" />
			<template #footer>
				<div>
					<ds-button :type="BUTTON_TYPES.OUTLINED" @click="showModal = false">
						{{ t('ds.globals.confirmation') }}
					</ds-button>
				</div>
			</template>
		</ds-modal>
		<ds-card>
			<template #content>
				<div class="ds-surveyQuestionOpenEnded__header">
					<label class="ds-surveyQuestionOpenEnded__title" :for="inputId">
						{{ title }}
					</label>
					<icon-button
						v-if="$slots.explanation"
						class="ds-surveyQuestionOpenEnded__explanation"
						:color="ICON_BUTTON_COLORS.NEUTRAL_WEAK"
						:icon="ICONS.FA_CIRCLE_QUESTION"
						:size="ICON_SIZES.MEDIUM"
						:touchable="false"
						@click="showModal = true"
					/>
				</div>
				<div class="ds-surveyQuestionOpenEnded__content">
					<survey-question-textarea
						:id="inputId"
						class="ds-surveyQuestionOpenEnded__input"
						:disabled="state === SURVEY_QUESTION_STATES.DISABLED"
						:value="value"
						:placeholder="placeholder"
						@input="$emit('input', $event)"
					/>
				</div>
			</template>
		</ds-card>
	</div>
</template>

<style lang="scss" scoped>
@import '../../../../styles/settings/buttons';
@import '../../../../styles/settings/spacings';
@import '../../../../styles/settings/colors/tokens';
@import '../../../../styles/settings/typography/tokens';

.ds-surveyQuestionOpenEnded {
	&__header {
		display: flex;
		justify-content: space-between;
		margin-bottom: $space-s;
		// header without explanation iconButton has to be the same size as with iconButton
		min-height: $icon-button-medium-size;
	}

	&__title {
		@include heading-m-default-regular;

		align-self: center;
	}

	&__explanation {
		align-self: flex-start;
		margin-left: $space-4xs;
	}

	&__content {
		display: flex;
		flex-direction: column;
	}

	&__input {
		margin-top: $space-2xs;
	}
}
</style>

<script lang="ts">
import DsCard from '../../Cards/Card';
import DsModal from '../../Modal';
import DsButton, { BUTTON_TYPES } from '../../Buttons/Button';
import IconButton, { ICON_BUTTON_COLORS } from '../../Buttons/IconButton';
import { ICON_SIZES, ICONS } from '../../Icons/Icon';
import { SURVEY_QUESTION_STATES, SurveyQuestionState } from '../SurveyQuestion.consts';
import SurveyQuestionTextarea from '../SurveyQuestionTextarea.vue';
import { randomString } from '../../../utils/string';
import { useLegacyI18n } from '../../../composables/useLegacyI18n';

import { defineComponent } from 'vue';

export default defineComponent({
	name: 'SurveyQuestionOpenEnded',
	components: {
		SurveyQuestionTextarea,
		DsButton,
		DsCard,
		IconButton,
		DsModal,
	},
	props: {
		title: {
			type: String,
			required: true,
		},
		value: {
			type: String,
			required: true,
		},
		placeholder: {
			type: String,
			default: 'Wpisz swoj odpowied',
		},
		state: {
			type: String,
			default: SURVEY_QUESTION_STATES.DEFAULT,
			validator(state: SurveyQuestionState) {
				return Object.values(SURVEY_QUESTION_STATES).includes(state);
			},
		},
	},
	// TODO fix me when touching this file
	// eslint-disable-next-line vue/require-emit-validator
	emits: ['input'],
	setup() {
		const { t } = useLegacyI18n();

		return { t };
	},
	data() {
		return {
			showModal: false,
			inputId: 'survey-question-' + randomString(8),
			ICONS: Object.freeze(ICONS),
			ICON_BUTTON_COLORS: Object.freeze(ICON_BUTTON_COLORS),
			ICON_SIZES: Object.freeze(ICON_SIZES),
			BUTTON_TYPES: Object.freeze(BUTTON_TYPES),
			SURVEY_QUESTION_STATES: Object.freeze(SURVEY_QUESTION_STATES),
		};
	},
});
</script>
